// src/components/ProtectedRoute.jsx
import React from 'react';
import { useAuth } from '../contexts/AuthContext';
import { Navigate, Outlet } from 'react-router-dom';

// Este componente recebe a 'role' (papel) necessria para acessar as rotas filhas
const ProtectedRoute = ({ role }) => {
    const { userProfile, loading } = useAuth();

    // Enquanto a autenticao estiver carregando, mostramos uma mensagem
    if (loading) {
        return <div>Verificando autenticao...</div>;
    }

    // Se no houver perfil (usurio no logado), redireciona para o login
    if (!userProfile) {
        return <Navigate to="/login" replace />;
    }

    // Se uma 'role' especfica  necessria e o usurio no a tem, redireciona para a home
    if (role && userProfile.user_type !== role) {
        return <Navigate to="/" replace />;
    }

    // Se todas as verificaes passarem, renderiza a pgina solicitada (o componente <Outlet />)
    return <Outlet />;
};

export default ProtectedRoute;